import myaxios from '../utils/httpRequest'

const apiList = {
   add:`/addPrjInfo`,
   delete:`/deletePrjById`,
   find:`/getPrjByName`,
   edict:`/updatePrjInfo`
}

export default {
    addPrjInfo(data) {
        return myaxios({
            // headers: {
            //     'Content-Type': 'application/json' // 设置请求头部为 JSON 类型
            // },
            method:'POST',
            url:apiList.add,
            data:data,
        })
    },
    deletePrjById(id) {
        return myaxios({
            method:'GET',
            url:apiList.delete,
            prjId:id,
        })
    },
    //x-www-form-urlencoded用params传参 或者url拼接?key=value&key=value
    getPrjByName(name) {
        return myaxios({
            methode:'GET',
            url:apiList.find,
            params:{
                projectName:name,
            }
        })
    },
    updatePrjInfo(data) {
        return myaxios({
            method:'POST',
            url:apiList.edict,
            data,
        })
    },
}