<!-- 控制文件上传类型的回调提示
:before-upload="beforeUpload"
beforeUpload(file) {
  const fileSuffix = file.name.substring(file.name.lastIndexOf(".") + 1);
  const whiteList = ["jpg", "png"];
  if (whiteList.indexOf(fileSuffix) === -1) {
    this.message({message:"上传文件只能是 jpg,png格式", type: 'warning'});
    return false;
  }
}, 
-->
        <el-upload
