        // checkStatus:'未审核',  //审核状态：后台处理了resultStatus=1
      //全国级
        descCountry:'',  //全国成果描述
        fileListCountry:[],
      //区域级
        descRegion:'',  //区域成果描述
        fileListRegion:[],
        //空间范围
        spatical:{
          southWestLatL:'',
