          northEastLatB:'',
          centerline:'',
          reflectzone:'',
          southWestDisX:'',
          southWestDisY:'',
          northEastDisX:'',
          northEastDisY:'',
        },
      //项目级
        descDemo:'',  //项目成果描述
        fileListDemo:[], 
        fileListDemoCheck:[],
        //新添项目表单
        demoForm: {
          name: '',
          code: '',
          employer: '',
          constractor: '',
          start:'',
          end:'',
          startname:'',
          endname:'',
          startroad:'',
          endroad:'',
        },
        
        selectDemo:'',   //选择项目
        //项目勘察
        exploreStage:'',
        linePlan:'',
        //空间范围
        demoSpatical:{
          southWestLatL:'',
          northEastLatL:'',
          southWestLatB:'',
          northEastLatB:'',
          centerline:'',
          reflectzone:'',
          southWestDisX:'',
          southWestDisY:'',
          northEastDisX:'',
          northEastDisY:'',
        },
      //工点级
        descDot:'',  //工点成果描述
        fileListDot:[],
        fileListDotCheck:[],
        //新添加工点项目表单
        dotDemoForm: {
          name: '',
          code: '',
          employer: '',
          constractor: '',
          start:'',
          end:'',
          startname:'',
          endname:'',
          startroad:'',
          endroad:'',
        },
        //添加工点表单 
        dotForm: {
          name:'',
          type:'',
          address:'',
          startname:'',
          startroad:'',
          endname:'',
          endroad:'',
          selectPrjCode:'',
        },
        selectDotDemo:'', //选择工点项目
        selectDot:'',  //选择工点
        //工点勘察
        dotExploreStage:'',
        dotLinePlan:'',
        //空间范围
        dotSpatical:{
          southWestLatL:'',
          northEastLatL:'',
          southWestLatB:'',
          northEastLatB:'',
          centerline:'',
          reflectzone:'',
          southWestDisX:'',
          southWestDisY:'',
          northEastDisX:'',
          northEastDisY:'',
        },
      },
      
      // 表单校验
      rules: {
        name: [
          { required: true, message: '请输入成果名称', trigger: 'blur' }
        ],
        level: [
          { required: true, message: '请输入成果级别', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择成果类型', trigger: 'change' }
        ],
        format: [
          { required: true, message: '请选择数据格式', trigger: 'change' }
        ],
        attachment:[
          { required: true, message: '请选择附件上传', trigger: 'blur'}
        ],
        addProName:[
          { required:true, message:'请添加项目', trigger:'blur'}
        ],
        addDotName:[
          { required:true, message:'请添加工点', trigger:'blur'}
        ],
        exploreStage:[
          { required:true, message:'请选择勘探阶断', trigger:'change'}
        ],
        checkfile:[
          { required:true, message:'请选校审文件上传', trigger:'blur'}
        ],
      }
    };
  },
  methods:{
    //图上传
    imgHandlePreview(file) {
      // this.showSelectedImage = URL.createObjectURL(file.raw);
      this.showImgs.push(URL.createObjectURL(file.raw));
      this.form.fileListAbri.push(file);
      console.log("缩略图fileListAbri",this.form.fileListAbri);
      console.log("showIMGS",this.showImgs)
    },
    //图更新
    imgHandleReplace(file){
      this.showImgs = [];
      this.form.fileListAbri = [];
      this.showImgs.push(URL.createObjectURL(file.raw));
      this.form.fileListAbri.push(file);
      console.log("缩略图fileListAbri",this.form.fileListAbri);
      console.log("showIMGS",this.showImgs)
    },
    // imgHandleUpdateClick(){
    //   this.showImgs = [];
    //   this.form.fileListAbri = [];
    // },
    //附件上传-国家
    attachCountryChange(response,file){
      this.form.fileListCountry = file;
      console.log("fileListCountry",this.form.fileListCountry);
      // console.log(response)

    },
    attachCountryRemove(file) {
      console.log("file",file);
      //这里可以处理要删除的东西
    },
    
    //附件上传-区域
    attachRegionChange(response,file) {
      this.form.fileListRegion = file;
      console.log("fileListRegion",this.form.fileListRegion);

    },
    attachRegionRemove(file) {
      console.log("file",file);
    },
    //附件上传-项目
    attachDemoChange(response,file) {
      this.form.fileListDemo = file;
      console.log("fileListDemo",this.form.fileListDemo);
    },
    attachDemoRemove(file) {
      console.log("file",file);
    },
    checkDemoChange(response,file) {
      this.form.fileListDemoCheck = file;
      console.log("fileListDemoCheck",this.form.fileListDemoCheck);
    },
    checkDemoRemove(file) {
      console.log("file",file);
    },
    //附件上传-工点
    attachDotChange(response,file) {
      this.form.fileListDot = file;
      console.log("fileListDot",this.form.fileListDot);
    },
    attachDotRemove(file) {
      console.log("file",file);
    },
    checkDotChange(response,file) {
      this.form.fileListDotCheck = file;
      console.log("fileListDotCheck",this.form.fileListDotCheck);
    },
    checkDotRemove(file) {
      console.log("file",file);
    },
    //axios
    //获取提交时间
    setSubmitTime() {
      const currentTime = new Date();
      const year = currentTime.getFullYear();
      const month = String(currentTime.getMonth() + 1).padStart(2, '0');
      const day = String(currentTime.getDate()).padStart(2, '0');
      const hours = String(currentTime.getHours()).padStart(2, '0');
      const minutes = String(currentTime.getMinutes()).padStart(2, '0');
      return `${year}-${month}-${day} ${hours}:${minutes}`
    },
    //工点级选择项目后返回对应code到添加工点表单
    async getprjCode() {
      let res = await DemoMana.getPrjByName(this.form.selectDotDemo);
      console.log("选中项目对应的信息",res.data.data[0].worksiteInfo);
      this.form.dotForm.selectPrjCode = res.data.data[0].projectCode;
      // this.$nextTick(()=>{
        this.newDotList = res.data.data[0].worksiteInfo.map(item => item.worksiteName);
      // })
    },
    //获取项目列表
    async getProjects() {
      let res = await DemoMana.getPrjByName();
      this.$nextTick(()=>{
        this.newDemoList = res.data.data;
      })
      // console.log("项目列表",this.newDemoList);
    },
    //获取工点列表
    async getDots() {
      let res = await DotMana.getAllWorksite();
      this.$nextTick(()=>{
        this.newDotList = res.data.data;
      })
      // console.log("工点列表",this.newDotList);
    },
    //添加新项目
    async addNewDemo(type) {
      if(type === '项目'){
        let demoDatas = {
          buildUnit:this.form.demoForm.employer,
          constructUnit:this.form.demoForm.constractor,
          startMileage:Number(this.form.demoForm.startroad),
          endMileage:Number(this.form.demoForm.endroad),
          startPrefix:this.form.demoForm.startname,
          endPrefix:this.form.demoForm.endname,
          projectStart:this.form.demoForm.start,
          projectEnd:this.form.demoForm.end,
          projectName:this.form.demoForm.name,
          projectCode:this.form.demoForm.code,
        }
        let res = await DemoMana.addPrjInfo(demoDatas);
        if(res.data.code === 200){
          this.$message({
            type:'success',
            message:'添加成功'
          });
          this.getProjects();
        }
      }
      else if(type === '工点项目'){
        let demoDatas = {
          buildUnit:this.form.dotDemoForm.employer,
          constructUnit:this.form.dotDemoForm.constractor,
          startMileage:Number(this.form.dotDemoForm.startroad),
          endMileage:Number(this.form.dotDemoForm.endroad),
          startPrefix:this.form.dotDemoForm.startname,
          endPrefix:this.form.dotDemoForm.endname,
          projectStart:this.form.dotDemoForm.start,
          projectEnd:this.form.dotDemoForm.end,
          projectName:this.form.dotDemoForm.name,
          projectCode:this.form.dotDemoForm.code,
        }
        let res = await DemoMana.addPrjInfo(demoDatas);
        if(res.data.code === 200){
          this.$message({
            type:'success',
            message:'添加成功'
          });
          this.getProjects();
        }
      }
      
    },
    //添加新工点
    async addNewDot() {
      let dotDatas = {
        startMileage:Number(this.form.dotForm.startroad),
        endMileage:Number(this.form.dotForm.endroad),
        startPrefix:this.form.dotForm.startname,
        endPrefix:this.form.dotForm.endname,
        worksiteAddr:this.form.dotForm.address,
        worksiteName:this.form.dotForm.name,
        worksiteType:this.form.dotForm.type,
        projectCode:this.form.dotForm.selectPrjCode,
      }
      let res = await DotMana.addWorkSiteInfo(dotDatas);
      console.log(res);
      if(res.data.code === 200){
        this.$message({
          type:'success',
          message:`已成功添加工点`
        });
        this.getDots();
      }
    },
    submitForm(formNames) {
      let isAllValid = true;
      if(this.form.level === '全国级'){
        formNames.push('formdown1')
      } else if(this.form.level === '区域级'){
        formNames.push('formdown2')
      } else if(this.form.level === '项目级'){
        formNames.push('formdown3')
      } else if(this.form.level === '工点级'){
        formNames.push('formdown4')
      }
      console.log("当前formNames",formNames);
      formNames.forEach(formName => {
        this.$refs[formName].validate(valid => {
          if (!valid) {
            isAllValid = false;
          }
        });
      });
      if (isAllValid) {
        // 所有表单都通过验证，执行相应的操作
        this.addGeoResult();
      } else {
        return false;
      }
    },

    addGeoResult(){
      let annexesAbri = this.form.fileListAbri.map(item => ({
        annexData: item.raw,
        annexType: 3,
        order: 1,
      }));
      if(this.form.level === '全国级' && this.form.name && this.form.format && this.form.type && this.form.fileListCountry[0]){
          this.loading = true;
          let annexesCountry = this.form.fileListCountry.map(item => ({
          annexData: item.raw,
          annexType: 1,
          order: 1,
        }));
        //处理details其他数据
        let jsonDetails = JSON.stringify({
          type:this.form.type,
          level:this.form.level,
          submitTime:this.setSubmitTime(),
          // checkStatus:this.form.checkStatus,
          personName:this.form.personName,
        });
        // let annexes = annexesCountry.concat(annexesAbri);
        // console.log("全国级:附件+缩略图",annexes);
        let myformData = new FormData();
        for(let i = 0; i<annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesAbri[i].annexData);
          myformData.append(`annexes[${i}].annexType`,3);
          myformData.append(`annexes[${i}].order`,1);
        };
        for(let i = annexesAbri.length; i<annexesCountry.length+annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesCountry[i-annexesAbri.length].annexData);
          myformData.append(`annexes[${i}].annexType`,1);
          myformData.append(`annexes[${i}].order`,1);
        }
        myformData.append('resultName',this.form.name);
        myformData.append('contextType',this.form.format);
        myformData.append('resultRemark',this.form.descCountry);
        myformData.append('resultLevel',this.form.level);
        myformData.append('resultType',this.form.type);
        myformData.append('resultDetails',jsonDetails);
        //打印myformData
        for (var key of myformData.keys()) {
            console.log("key:" + key + " value:" + myformData.get(key));
        }
        myaxios.post("/addGeoResult",myformData).then(res =>{
          console.log(res);
          if(res.data.code === 200){
            this.$message({
              type: 'success',
              message: '上传成功'
            });
            this.clearFormDatas();
            this.loading = false;
          }else if(res.data.code === 500){
            this.$message({
              type: 'warning',
              message: `${res.data.data}(不同文件类型的文件不可重复)`
            });
            this.loading = false;
          }
        }).catch(err => {
          console.log(err);
          this.$message({
              type: 'danger',
              message: '上传失败'
            });
        })  
      }
      else if(this.form.level === '区域级' && this.form.name && this.form.format && this.form.type && this.form.fileListRegion[0]){
        this.loading = true;
        let annexesRegion = this.form.fileListRegion.map(item => ({
          annexData: item.raw,
          annexType: 1,
          order: 1,
        }));
        //处理details其他数据
        let jsonDetails = JSON.stringify({
          type:this.form.type,
          level:this.form.level,
          spatical:this.form.spatical,
          submitTime:this.setSubmitTime(),
          personName:this.form.personName,
        });
        let myformData = new FormData();
        for(let i = 0; i<annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesAbri[i].annexData);
          myformData.append(`annexes[${i}].annexType`,3);
          myformData.append(`annexes[${i}].order`,1);
        };
        for(let i = annexesAbri.length; i<annexesRegion.length+annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesRegion[i-annexesAbri.length].annexData);
          myformData.append(`annexes[${i}].annexType`,1);
          myformData.append(`annexes[${i}].order`,1);
        }
        myformData.append('resultName',this.form.name);
        myformData.append('contextType',this.form.format);
        myformData.append('resultRemark',this.form.descRegion);
        myformData.append('resultLevel',this.form.level);
        myformData.append('resultType',this.form.type);
        myformData.append('resultDetails',jsonDetails);
        //打印myformData
        for (var key of myformData.keys()) {
            console.log("key:" + key + " value:" + myformData.get(key));
        }
        myaxios.post("/addGeoResult",myformData).then(res =>{
          console.log(res);
          if(res.data.code === 200){
            this.$message({
              type: 'success',
              message: '上传成功'
            });
            this.clearFormDatas();
            this.loading = false;
          }else if(res.data.code === 500){
            this.$message({
              type: 'warning',
              message: `${res.data.data}(不同文件类型的文件不可重复)`
            });
            this.loading = false;
          }
        }).catch(err => {
          console.log(err);
          this.$message({
              type: 'danger',
              message: '上传失败'
            });
        })  
      }
      else if(this.form.level === '项目级' && this.form.name && this.form.format && this.form.type && this.form.fileListDemo[0] && this.form.fileListDemoCheck[0]){
        this.loading = true;
        console.log(this.form.level);
        let annexesDemo = this.form.fileListDemo.map(item => ({
          annexData: item.raw,
          annexType: 1,
          order: 1,
        }));
        let annexesDemoCheck = this.form.fileListDemoCheck.map(item => ({
          annexData: item.raw,
          annexType: 2,
          order: 1,
        }))
        // let annexes = annexesAbri.concat(annexesDemo,annexesDemoCheck);
        // console.log("项目级:附件+审核+缩略图",annexes);
        let myformData = new FormData();
        for(let i = 0; i<annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesAbri[i].annexData);
          myformData.append(`annexes[${i}].annexType`,3);
          myformData.append(`annexes[${i}].order`,1);
        };
        for(let i = annexesAbri.length; i<annexesDemo.length+annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesDemo[i-annexesAbri.length].annexData);
          myformData.append(`annexes[${i}].annexType`,1);
          myformData.append(`annexes[${i}].order`,1);
        }
        for(let i = annexesDemo.length+annexesAbri.length; i<annexesDemo.length+annexesAbri.length+annexesDemoCheck.length;i++){
          myformData.append(`annexes[${i}].annexData`,annexesDemoCheck[i-annexesAbri.length-annexesDemo.length].annexData);
          myformData.append(`annexes[${i}].annexType`,2);
          myformData.append(`annexes[${i}].order`,1);
        }
        myformData.append('resultName',this.form.name);
        myformData.append('contextType',this.form.format);
        myformData.append('resultRemark',this.form.descDemo);
        myformData.append('resultLevel',this.form.level);
        myformData.append('resultType',this.form.type);
        let jsonDetails = JSON.stringify({
          type:this.form.type,
          level:this.form.level,
          spatical:this.form.demoSpatical,
          personName:this.form.personName,
          exploreStage:this.form.exploreStage,
          linePlan:this.form.linePlan,
          submitTime:this.setSubmitTime(),
          //此处接口获得
          demoFormName:this.form.selectDemo,
        });
        myformData.append('resultDetails',jsonDetails);
        //打印myformData
        for (var key of myformData.keys()) {
            console.log("key:" + key + " value:" + myformData.get(key));
        };
        myaxios.post("/addGeoResult",myformData).then(res =>{
          console.log(res);
          if(res.data.code === 200){
            this.$message({
              type: 'success',
              message: '上传成功'
            });
            this.clearFormDatas();
            this.loading = false;
          }else if(res.data.code === 500){
            this.$message({
              type: 'warning',
              message: `${res.data.data}(不同文件类型的文件不可重复)`
            });
            this.loading = false;
          }
        }).catch(err => {
          console.log(err);
          this.$message({
              type: 'danger',
              message: '上传失败'
            });
        })  
      }else if(this.form.level === '工点级'&& this.form.name && this.form.format && this.form.type && this.form.fileListDot[0] && this.form.fileListDotCheck[0]){
        this.loading = true;
        let annexesDot = this.form.fileListDot.map(item => ({
          annexData: item.raw,
          annexType: 1,
          order: 1,
        }));
        let annexesDotCheck = this.form.fileListDotCheck.map(item => ({
          annexData: item.raw,
          annexType: 2,
          order: 1,
        }))
        let myformData = new FormData();
        for(let i = 0; i<annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesAbri[i].annexData);
          myformData.append(`annexes[${i}].annexType`,3);
          myformData.append(`annexes[${i}].order`,1);
        };
        for(let i = annexesAbri.length; i<annexesDot.length+annexesAbri.length; i++){
          myformData.append(`annexes[${i}].annexData`,annexesDot[i-annexesAbri.length].annexData);
          myformData.append(`annexes[${i}].annexType`,1);
          myformData.append(`annexes[${i}].order`,1);
        }
        for(let i = annexesDot.length+annexesAbri.length; i<annexesDot.length+annexesAbri.length+annexesDotCheck.length;i++){
          myformData.append(`annexes[${i}].annexData`,annexesDotCheck[i-annexesAbri.length-annexesDot.length].annexData);
          myformData.append(`annexes[${i}].annexType`,2);
          myformData.append(`annexes[${i}].order`,1);
        }
        myformData.append('resultName',this.form.name);
        myformData.append('contextType',this.form.format);
        myformData.append('resultRemark',this.form.descDot);
        myformData.append('resultLevel',this.form.level);
        myformData.append('resultType',this.form.type);
        let jsonDetails = JSON.stringify({
          type:this.form.type,
          level:this.form.level,
          spatical:this.form.dotSpatical,
          exploreStage:this.form.dotExploreStage,
          linePlan:this.form.dotLinePlan,
          personName:this.form.personName,
          submitTime:this.setSubmitTime(),
          //此处接口获得
          demoFormName:this.form.selectDotDemo,
          dotFormName:this.form.selectDot,
        });
        myformData.append('resultDetails',jsonDetails);
        //打印myformData
        for (var key of myformData.keys()) {
            console.log("key:" + key + " value:" + myformData.get(key));
        };
        myaxios.post("/addGeoResult",myformData).then(res =>{
          console.log(res);
          if(res.data.code === 200){
            this.$message({
              type: 'success',
              message: '上传成功'
            });
            this.clearFormDatas();
            this.loading = false;
          }else if(res.data.code === 500){
            this.$message({
              type: 'warning',
              message: `${res.data.data}(不同文件类型的文件不可重复)`
            });
            this.loading = false;
          }
        }).catch(err => {
          console.log(err);
          this.$message({
              type: 'danger',
              message: '上传失败'
            });
        })  
      }else {
        this.$message({
          type: 'warning',
          message: '请完整填写必填内容'
        });
      };  
      //调common.js中的方法清理
      // clearForm(this.form);

      // this.clearFormDatas();
      this.showImgs = [];
      console.log("form",this.form)
    },  
    clearBlur(formNames){
      // 重置所有的错误变量
      formNames.forEach(formName => {
        this.$refs[formName].clearValidate();
      });
    },
    clearFormDatas(){
      this.form = {
      //公共部分
        name: '', //成果名称
        level: '', //成果级别
        type: '', //成果类型
        format: '', //数据格式
        fileListAbri:[],  //存放缩略图文件
        personName:'wb',  //后期根据token存用户姓名   
        // checkStatus:'未审核',  //审核状态：后台处理了resultStatus=1
      //全国级
        descCountry:'',  //全国成果描述
        fileListCountry:[],
      //区域级
        descRegion:'',  //区域成果描述
        fileListRegion:[],
        //空间范围
        spatical:{
          southWestLatL:'',
          northEastLatL:'',
          southWestLatB:'',
          northEastLatB:'',
          centerline:'',
          reflectzone:'',
          southWestDisX:'',
          southWestDisY:'',
          northEastDisX:'',
          northEastDisY:'',
        },
      //项目级
        descDemo:'',  //项目成果描述
        fileListDemo:[], 
        fileListDemoCheck:[],
        //新添项目表单
        demoForm: {
          name: '',
          code: '',
          employer: '',
          constractor: '',
          start:'',
          end:'',
          startname:'',
          endname:'',
          startroad:'',
          endroad:'',
        },
        selectDemo:'',   //选择项目
        //项目勘察
        exploreStage:'',
        linePlan:'',
        //空间范围
        demoSpatical:{
          southWestLatL:'',
          northEastLatL:'',
          southWestLatB:'',
          northEastLatB:'',
          centerline:'',
          reflectzone:'',
          southWestDisX:'',
          southWestDisY:'',
          northEastDisX:'',
          northEastDisY:'',
        },
      //工点级
        descDot:'',  //工点成果描述
        fileListDot:[],
        fileListDotCheck:[],
        //新添加工点项目表单
        dotDemoForm: {
          name: '',
          code: '',
          employer: '',
          constractor: '',
          start:'',
          end:'',
          startname:'',
          endname:'',
          startroad:'',
          endroad:'',
        },
        //添加工点表单 
        dotForm: {
          name:'',
          type:'',
          address:'',
          startname:'',
          startroad:'',
          endname:'',
          endroad:'',
          selectPrjCode:'',
        },
        selectDotDemo:'', //选择工点项目
        selectDot:'',  //选择工点
        //工点勘察
        dotExploreStage:'',
        dotLinePlan:'',
        //空间范围
        dotSpatical:{
          southWestLatL:'',
          northEastLatL:'',
          southWestLatB:'',
          northEastLatB:'',
          centerline:'',
          reflectzone:'',
          southWestDisX:'',
          southWestDisY:'',
          northEastDisX:'',
          northEastDisY:'',
        },
      };
      this.showImgs = [];
      this.clearBlur(['formup','formdown1','formdown2','formdown3','formdown4'])
    },
  },
  
  mounted(){
    this.getProjects();
    this.getDots();
  },
};
</script>

