import myaxios from '../utils/httpRequest'

const apiList = {
   add:`/addWorkSiteInfo`,
   delete:`/deleteWSById`,
   find:`/getAllWorksite`,
   edict:`/updateWorkSiteInfo`
}

export default {
    addWorkSiteInfo(data) {
        return myaxios({
            method:'POST',
            url:apiList.add,
            data,
        })
    },
    deleteWSById(id) {
        return myaxios({
            method:'GET',
            url:apiList.delete,
            worksiteId:id,
        })
    },
    getAllWorksite() {
        return myaxios({
            methode:'GET',
            url:apiList.find,
        })
    },
    updateWorkSiteInfo(data) {
        return myaxios({
            method:'POST',
            url:apiList.edict,
            data,
        })
    },
}